@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@400;700&family=Ubuntu&display=swap);
*,*::before,*::after,body{margin:0;padding:0;list-style:none;text-decoration:none}

.accordion__wrapper{padding:40px 0}.accordion__title{font-family:"Oxygen",sans-serif;color:#27743D;font-weight:600;font-size:24px;line-height:32px;padding:10px;border-bottom:#63A557 1px solid}.accordion__category{padding:10px 5px}.category__header{padding:10px 5px;border-bottom:#63A557 solid 2px;display:flex;justify-content:space-between}.category__title,.category__title--chevrondown,.category__title--chevronup{font-family:"Oxygen",sans-serif;color:#349145;font-weight:600;font-size:20px;line-height:38px}.category__title--chevrondown{font-weight:400;transform:rotate(180deg) scaleX(1.5) scaleY(0.7)}.category__title--chevronup{font-weight:400;transform:scaleX(1.5) scaleY(0.7)}@media screen and (min-width: 768px){.category__section{min-width:550px;display:flex;justify-content:space-around}.category__header{min-width:550px}}.hidden{display:none}.service__title{font-family:"Oxygen",sans-serif;color:#349145;font-weight:600;font-size:16px;line-height:24px;padding:10px 5px;-webkit-animation:0.3s dropdown ease-in;animation:0.3s dropdown ease-in}.service__list{font-family:"Ubuntu",sans-serif;color:#444;font-weight:600;font-size:14px;line-height:20px;padding:5px;-webkit-animation:0.3s dropdown ease-in;animation:0.3s dropdown ease-in}@-webkit-keyframes dropdown{0%{transform:translateY(-10px);opacity:0}75%{transform:translateY(-5px);opacity:30}100%{transform:translateY(0px);opacity:100}}@keyframes dropdown{0%{transform:translateY(-10px);opacity:0}75%{transform:translateY(-5px);opacity:30}100%{transform:translateY(0px);opacity:100}}*,*::before,*::after,body{margin:0;padding:0;list-style:none;text-decoration:none}.app__container{width:100%;display:flex;justify-content:center;align-items:center;padding:30px 20px}.loader{border:16px solid #63A557;border-top:16px solid #27743D;border-radius:50%;width:80px;height:80px;-webkit-animation:spin 2s linear infinite;animation:spin 2s linear infinite}@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

